.background-content {
    background-color: $color-12;
    height: 100%;
    padding: 40px 30px;
    flex-wrap: wrap;
}

.card-img-top{
    width: 210px;
    height: 470px;
    align-self: center;
}

.card-deck-content{
    padding: 25px;
}

.card{
    padding: 5px;
}

.primary-btn {
    align-items: center;
    background-color: $color-11;
    border-radius: 100px;
    border-style: none;
    color: $color-2;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 15px;
    height: 38px;
    line-height: 19px;
    text-align: center;
    width: 100%;
}

.primary-btn:hover {
    background-color: $color-2;
    color: $color-1;
    border: 2px solid $color-11;
}

